      <span class="hljs-tag">&lt;<span class="hljs-name">vc-primitive</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"primitive"</span> <span class="hljs-attr">:appearance</span>=<span class="hljs-string">"appearance"</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">vc-instance-geometry</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"geometry"</span> <span class="hljs-attr">:geometry.sync</span>=<span class="hljs-string">"geometry"</span> <span class="hljs-attr">:attributes</span>=<span class="hljs-string">"attributes"</span> <span class="hljs-attr">:model-matrix</span>=<span class="hljs-string">"modelMatrix"</span>&gt;</span>
